@import "../helpers/variables";
@import "../helpers/animation";
@import "../helpers/mixins";

.skills {
  padding: $sectionPadding 0;
  .container {
    .row > div {
      flex-grow: 1;
    }
    .skill-box {
      padding: $mainPadding + 10;
      background-color: $boxBackground;
      border-radius: 6px;
      position: relative;
      transition: 0.7s;
      margin-bottom: 20px;
      flex-grow: 1fr;
      transform: translateY(-25px);
      opacity: 0;
      @media (min-width: 767px) and (max-width: 1200px) {
        height: 230px;
      }
      &:hover {
        box-shadow: 0px 0px 9px -2px rgba($color: $secondaryColor, $alpha: 0.9) inset;
        background-color: rgba($color: $boxBackground, $alpha: 0.1);
        backdrop-filter: blur(10px);
        .skill-description {
          color: #ddddddd1;
        }
      }
      .skill-Pug {
        img {
          width: 97px !important;
          height: 94px !important;
        }
      }
      .skill-Jest {
        img {
          width: 112px !important;
          height: 119px !important;
        }
      }
      .skill-Git {
        img {
          align-self: flex-end;
        }
      }
      &:hover .skill-icon img {
        animation: up-down 0.5s infinite alternate ease;
      }

      .skill-icon {
        width: 80px;
        height: 80px;
        display: block;
        margin: auto;
        position: absolute;
        top: -45px;
        right: -10px;
        display: flex;
        align-items: center;
        justify-content: center;
        animation-play-state: paused;
        border-radius: 50%;
        background-color: $backgroundColor;
        img {
          width: 40px;
          height: 60px;
        }
      }
      .skill-title {
        color: $primaryColor;
        width: fit-content;
        display: block;
        transition-duration: 0.3s;
        color: $secondaryColor;
      }
      .skill-description {
        margin-top: $mainPadding;
        color: $paragrahColor;
        font-size: 14px;
        line-height: 1.8;
        transition-duration: 0.7s;
        .skill-link {
          color: $secondaryColor;
          transition-duration: 0.3s;
          &:hover {
            color: $altSecondaryColor;
          }
        }
      }
    }
  }
}

@for $i from 1 through 10 {
  .count-#{$i} {
    @include fade-in(calc($i / 5));
  }
}
