@import "../helpers/animation";

@mixin move-right($delay) {
  width: fit-content;
  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: $backgroundColor;
    top: 0;
    right: 0;
    animation: showRight 1s #{$delay} forwards ease-in-out;
    z-index: 10;
  }
}

@mixin fade-in($delay) {
  animation: fade-in 0.7s forwards alternate ease paused #{$delay}s;
}
