// filling the title color
@keyframes fill-color {
  100% {
    width: 100%;
  }
}

// display the content from the right 
@keyframes showRight {
  100% {
    width: 0;
  }
}


//============ start icons animation ============
@keyframes up-down {
  to {
    transform: translateY(-10px);
  }
}

@keyframes fade-in {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
//============ end icons animation ==============
