@import "../helpers/variables";
@import "../helpers/animation";
@import "../helpers/mixins";

.landing {
  height: 100vh;
  background: url("../imgs/portfolio.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  position: relative;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    .info {
      .name {
        color: $primaryColor;
        font-size: 55px;
        margin: 0;
        text-transform: capitalize;
        word-spacing: -8.2px;
        position: relative;
        @include move-right(0.9s);
      }
      .job-title {
        margin: 5px 0 20px;
        text-transform: capitalize;
        width: fit-content;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: 1px #5eead4;
        white-space: nowrap;
        &::after {
          content: attr(data-text);
          position: absolute;
          overflow: hidden;
          left: 0;
          top: 0;
          width: 0;
          -webkit-text-fill-color: #5eead4;
          animation: fill-color 4s ease-in-out infinite alternate 2s;
        }
        @include move-right(1.2s);
        position: relative;
      }
      .description {
        color: $paragrahColor;
        max-width: 550px;
        line-height: 1.7;
        font-size: 18px;
        @include move-right(1.7s);
        position: relative;
      }
      .social-area {
        margin-top: 40px;
        @include move-right(2.5s);
        position: relative;
        .social-link {
          padding: 5px;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          color: $primaryColor;
          width: 60px;
          height: 60px;
          border-radius: 50%;
          border: 2px solid $secondaryColor;
          margin-right: 10px;
          font-size: 28px;
          transition-duration: 0.4s;
          overflow: hidden;
          position: relative;
          &::after {
            content: "";
            position: absolute;
            width: 0;
            height: 100%;
            background-color: $secondaryColor;
            left: 0;
            top: 0;
            transition: 0.4s;
          }
          &:hover {
            color: $backgroundColor;
            border-color: $backgroundColor;
          }
          &:hover::after {
            width: 100%;
          }
          i {
            z-index: 2;
          }
        }
      }
    }
    .profile-img {
      position: relative;
      @include move-right(2.3s);
      &::before {
        top: -20px;
      }
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        right: 0;
        top: 0;
        transition-duration: 2.5s;
      }
      &:hover::after {
        background-color: #0f172a85;
      }
      img {
        position: relative;
        max-width: 100%;
        margin-top: -50px;
        margin-right: -100px;
        z-index: -1;
      }
    }
  }
}
