@import "../helpers/variables";
@import "../helpers/animation";
@import "../helpers/mixins";

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background-color: $backgroundColor;
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    .logo {
      color: $primaryColor;
      font-size: 40px;
      text-transform: capitalize;
      @include move-right(0.2s);
      position: relative;
    }
    .nav-links {
      @include move-right(0.5s);
      position: relative;
      .nav-link {
        color: $primaryColor;
        margin-left: 15px;
        font-size: 18px;
        display: inline-flex;
        transition-duration: 0.3s;
        text-transform: capitalize;
        &:hover,
        &.active {
          color: $secondaryColor;
        }
      }
    }
  }
}
